import React from 'react';
import Link from 'next/link';
import { Menu, PageHeader } from 'antd';
const AppLayout = ({children}) => {
    const dummy = {
        classname: "DSM"
    }
    return (
        <div>
            <Menu mode="horizontal">
                <Menu.Item key="name" style={{marginLeft: '20px'}}><Link href="/class"><a>{dummy.classname}</a></Link></Menu.Item>
                <Menu.Item key="class"><Link href="/class"><a>수업</a></Link></Menu.Item>
                <Menu.Item key="project"><Link href="/project"><a>과제</a></Link></Menu.Item>
                <Menu.Item key="group"><Link href="/group"><a>모둠</a></Link></Menu.Item>
                <Menu.Item key="setting"><Link href="/setting"><a>관리</a></Link></Menu.Item>
            </Menu>
        </div>
    )
}

export default AppLayout;